.wrapper{
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 90vh;
    background-color: rgba(9, 6, 12, 0.904);
}

section p{
    padding: 10px 0 30px;
}
footer{
    background-color: rgba(9, 6, 12, 0.904);
    color: white;
    text-align: center;
    padding: 30px 0;
}
.socials{
    display: flex;
    column-gap: 20px;
    justify-content: center;
    
}
.icon{
    font-size: 30px;
    cursor: pointer;
    transition: .3s all ease;
}
.icon:hover{
    color: rgb(161, 127, 255);
    
}
.social_wrap{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
.social_wrap p{
    font-size: 0px;
    opacity: 0;
    position: absolute;
    transition: .5s all ease;
    right:15px;
    top:0px;
}
.social_wrap:hover p{
    font-size: 14px;
    opacity: 1;
    right:-8px;
    top:-20px;
    transition: .3s all ease;
}

