.wrapper{
    background-color: rgba(9, 6, 12, 0.904);
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 10vh;
    padding: 0 20px;
    

}
.wrapper h1{
    color: white;
    font-family: 'Julee', cursive;
    font-size: 25px;
    color: rgb(161, 127, 255);
}

img{
    width: 40px;
    height: 40px;
}
.nav{
    display: flex;
    align-items: center;
    column-gap: 20px;
}

li{
    list-style: none;
}
.link{
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    border-color: rgb(98, 0, 255);
    transition: .3s all ease;
    font-weight: 600;
    
    
}
nav a.active{
    text-decoration: none;
    color: rgb(161, 127, 255);
    text-transform: uppercase;
    border-color: rgb(98, 0, 255);
    transition: .3s all ease;
    font-weight: 600;
    
}
.burger{
    display: none;
}

@media(max-width:778px){
    .nav{
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-top: 100px;
        width: 100%;
        height: 90vh;
        background-color: rgb(9, 6, 12);
        row-gap: 30px;
        position: absolute;
        font-size: 50px;
        top: 10vh;
        left: -100%;
        z-index: 99;
        transition: .5s all ease;
    }
    .nav_active{
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-top: 100px;
        width: 100%;
        height: 90vh;
        background-color: rgb(9, 6, 12);
        row-gap: 30px;
        position: absolute;
        font-size: 50px;
        top: 10vh;
        left: 0;
        z-index: 99;
        transition: .5s all ease;
    }
    .burger{
        width: 30px;
        height: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        cursor: pointer;
        overflow: hidden;
    }
    .line1{
        width: 30px;
        height: 3px;
        background-color: white;
        transition: .5s all ease;
    }
    .line1_active{
        width: 30px;
        height: 3px;
        background-color: white;
        transition: .5s all ease;
        margin-left: -20px;
        
        
    }
    .line2{
        width: 30px;
        height: 3px;
        background-color: white;
        transition: .5s all ease;
        margin: 0 auto;
    }
    .line2_active{
        width: 20px;
        height: 3px;
        background-color: white;
        transition: .5s all ease;   
    }
    .line3{
        width: 30px;
        height: 3px;
        background-color: white;
        transition: .5s all ease;
    }
    .line3_active{
        width: 30px;
        height: 3px;
        background-color: white;
        transition: .5s all ease;
        margin-left: 20px;
        
    }
}

