.wrapper{
    height: 90vh;
    grid-template-columns: 350px 350px;
    background-color: rgba(9, 6, 12, 0.904);
    color:white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.wrapper h1{
    font-size: 50px;
}
.location{
    margin-top: 20px;
}
.nomber{
    margin-top: 20px;
}
.nomber a{
    text-decoration: none;
    color:rgba(255, 255, 255, 0.466);
}
.mail{
    margin-top: 20px;
}
.mail a{
    text-decoration: none;
    color:rgba(255, 255, 255, 0.466);
}
a:hover{
    color: rgb(161, 127, 255);
}
@media(max-width:778px){
    .wrapper{
        height: 90vh;
        grid-template-columns: 350px 350px;
        background-color: rgba(9, 6, 12, 0.904);
        color:white;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding-bottom: 10vh;
    }
}