.wrapper{
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 90vh;
    background-color: rgba(9, 6, 12, 0.904); 
    background: rgb(42,42,42);
    background: linear-gradient(52deg, rgba(42,42,42,1) 29%, rgba(45,30,78,1) 100%); 
}


.title{
    font-size: 50px;
    color:white;
    text-align: center;
    padding-top: 100px;
    z-index: 2;
    position: relative;   
}
.title span{
    color: rgb(161, 127, 255);
}
.wrapper p{
    color: white;
    text-align: center;
    padding-top: 50px ;
    z-index: 2;
}
.button{
    text-align: center;
    margin-top: 50px;
    z-index: 2;
    
}
button {
    width: 120px;
    height: 40px;
    background-color: rgb(161, 127, 255);
    color: white;
    font-weight: 600;
    border: none;
    z-index: 2;
    cursor: pointer;
    
}
button:hover{
    background-color:rgba(98, 0, 255, 0.459); 
}
section{
    text-align: center;
}
section h1{
    padding-top: 30px;
}
section p{
    padding: 10px 0 30px;
}

.abstract{
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -160px;
    filter: invert(5);
    width: 300px;
    height: 300px;
    z-index: 1;
    animation: abstr infinite 10s linear;
}

@keyframes abstr {
    0%{
        scale: .5;
        rotate: -10deg;
        opacity: .2;
        
    }
    25%{
        scale: .7;
        rotate: -20deg;
        opacity: .8;
    }
    50%{
        scale: .8;
        rotate: -30deg;
        opacity: 1;
    }
    75%{
        scale: .7;
        rotate: -20deg;
        opacity: .8;
    }
    100%{
        scale: .5;
        rotate: -10deg;
        opacity: .2;
    }
}
@media(max-width:778px){
    .wrapper{
        background-position: center;
        background-size: cover;
        width: 100%;
        height: 90vh;
        background-color: rgba(9, 6, 12, 0.904);
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 10vh;
        
    }
    .abstract{
        position: absolute;
        top: 0;
        left: 50%;
        margin-left: -160px;
        filter: invert(5);
        width: 300px;
        height: 300px;
        z-index: 1;
        animation: abstr infinite 10s linear;
    }
}