.project{
    width: 350px;
    height: 200px;
    position: relative;
    cursor: pointer;
    transition: .6s all ease;
    box-shadow: 1px 1px  20px black;
    border-radius: 5px;   
}
.project:hover{
    scale: 1.1;
    border-radius: 5px;    
}
.project img{
    width: 350px;
    height: 200px;
    position: absolute;
    border-radius: 5px;
}

.project p{
    font-size: 25px;
    position: absolute;
    bottom: 10px;
    right: 10px;
    color: white;

}
.link{
    display: block;
    font-size: 50px;
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    text-decoration: none;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: .5s all ease;
    text-align: center;
    padding-top:60px;
    
}
.link:hover{
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(2, 2, 2, 0.925);
    color: white;
    text-decoration: none;
    width: 100%;
    height: 100%;
    opacity: 1;
    border-radius: 4px;
    transition: .5s all ease;
    text-align: center;
    font-weight: bold;   
}
@media(max-width:550px){
    .project{
        width: 300px;
        height: 200px;
        position: relative;
        cursor: pointer;
        transition: .6s all ease;
        box-shadow: 1px 1px  20px black;
        border-radius: 5px;
        
    }
    .project img{
        width: 300px;
        height: 200px;
        position: absolute;
        border-radius: 5px;
        
    
    
    }
}
