.wrapper{
    display: flex;
    min-height: 90vh;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    column-gap: 50px;
    row-gap: 20px;
    padding: 20px 0;
    background-color: rgba(9, 6, 12, 0.904);
}
@media(max-width:778px){
    .wrapper{
        display: flex;
        min-height: 90vh;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        row-gap: 20px;
        background-color: rgba(9, 6, 12, 0.904);
        padding: 20px 0;
    }
}

